<template>
  <v-container>
    <v-dialog v-model="solutionsDialog" max-width="900">
      <div v-if="isAddNew">
        <div class="modalHeader">
          <v-btn
            color="success"
            class="text-capitalize"
            small
            :disabled="loading"
            @click="solutionDialog = true"
          >
            <span>Add New</span>
          </v-btn>
        </div>
        <v-divider></v-divider>
      </div>
      <v-data-table
        :headers="headers"
        :items="solutions"
        :items-per-page="5"
        class="elevation-1 rounded-t-0"
        :loading="loading"
        loading-text="Loading... Please wait"
      >
        <template v-slot:[`item.remarks`]="{ item }">
          <div class="text-truncate" style="max-width: 250px">
            {{ item.remarks }}
          </div>
        </template>

        <template v-slot:[`item.created_at`]="{ item }">
          <span>{{ setCreatedAtFormat(item.created_at) }}</span>
        </template>
      </v-data-table>
    </v-dialog>

    <v-dialog
      v-if="solutionDialog"
      v-model="solutionDialog"
      :persistent="isPersistent"
      max-width="500px"
    >
      <v-form ref="solutionForm" @submit.prevent="onSubmit">
        <v-card :disabled="loadingForm">
          <v-card-title> Solution </v-card-title>
          <v-card-subtitle
            ><small>Note: All fields are required.</small></v-card-subtitle
          >
          <v-card-text>
            <v-row>
              <v-col cols="12">
                <v-text-field
                  ref="keyword"
                  v-model="solutionForm.keyword"
                  label="Keyword"
                  :rules="
                    saveStatus
                      ? [rules.required, rules.minimum(2), rules.maximum(100)]
                      : []
                  "
                  hide-details="auto"
                  outlined
                  dense
                ></v-text-field>
              </v-col>
              <v-col cols="12">
                <v-textarea
                  ref="remarks"
                  v-model="solutionForm.remarks"
                  label="Remarks"
                  :rules="
                    saveStatus
                      ? [rules.required, rules.minimum(2), rules.maximum(5000)]
                      : []
                  "
                  hide-details="auto"
                  rows="4"
                  outlined
                  dense
                ></v-textarea>
              </v-col>
            </v-row>
          </v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn @click="solutionDialog = false"> Close </v-btn>
            <v-btn color="success" type="submit"> Save </v-btn>
          </v-card-actions>
        </v-card>
      </v-form>
    </v-dialog>
  </v-container>
</template>

<script>
import { mapActions, mapState } from "vuex";
import moment from "moment";
import formRules from "@/helpers/formRules";

export default {
  name: "Solutions",

  props: ["isAddNew"],

  data: () => ({
    headers: [
      { text: "Keyword", value: "keyword" },
      { text: "Remarks", value: "remarks" },
      { text: "Date Created", value: "created_at" },
      { text: "Action", value: "action", sortable: false },
    ],
    rules: formRules,
    solutionDialog: false,
    isPersistent: false,
    solutionForm: {
      ticket_id: null,
      keyword: null,
      remarks: null,
    },
    saveStatus: false,
  }),

  created() {
    this.getRecords();
  },

  computed: {
    ...mapState(["loading", "solutions"]),
    ...mapState("myQueue", ["loadingForm"]),

    solutionsDialog: {
      get: function () {
        return true;
      },
      set: function () {
        this.$emit("closeSolutionsDialog");
      },
    },
  },

  methods: {
    ...mapActions("myQueue", ["createSolution"]),

    async getRecords() {
      this.$store.commit("setSolutions", []);
      await this.$store.dispatch("getSolutions", this.$route.params.id);

      this.solutionForm.ticket_id = this.$route.params.id;
    },

    async onSubmit() {
      this.saveStatus = true;
      await new Promise((r) => setTimeout(r, 200));
      if (!this.loadingForm) {
        if (this.$refs.solutionForm.validate()) {
          this.isPersistent = true;
          await this.createSolution(this.solutionForm);
          this.solutionsDialog = false;
          this.solutionDialog = false;
          this.$refs.solutionForm.reset();
          this.isPersistent = false;
        }
      }
    },

    setCreatedAtFormat(date) {
      return moment(date).format("MMM D, YYYY h:mm A");
    },
  },

  watch: {
    solutionDialog(visible) {
      if (visible) {
        setTimeout(() => {
          this.$refs.keyword.focus();
        }, 200);
      }
    },
  },
};
</script>

<style scoped>
.modalHeader {
  background-color: white;
  padding: 10px;
}
</style>
