<template>
  <v-dialog v-model="ticketLogsDialog" max-width="900">
    <v-data-table
      :headers="headers"
      :items="ticketLogs"
      :items-per-page="5"
      class="elevation-1"
      :loading="loading"
      loading-text="Loading... Please wait"
    >
      <template v-slot:[`item.created_at`]="{ item }">
        <span>{{ setCreatedAtFormat(item.created_at) }}</span>
      </template>
    </v-data-table>
  </v-dialog>
</template>

<script>
import { mapState } from "vuex";
import moment from "moment";

export default {
  name: "TicketLogs",

  data: () => ({
    headers: [
      { text: "Category", value: "category.name" },
      { text: "Status", value: "ticket_status.name" },
      { text: "Priority", value: "priority_status.name" },
      { text: "Department", value: "department.name" },
      { text: "Assigned To", value: "assignee.full_name" },
      { text: "Date Created", value: "created_at" },
    ],
  }),

  created() {
    this.$store.commit("setTicketLogs", []);
    this.$store.dispatch("getTicketLogs", this.$route.params.id);
  },

  computed: {
    ...mapState(["loading", "ticketLogs"]),

    ticketLogsDialog: {
      get: function () {
        return true;
      },
      set: function () {
        this.$emit("closeTicketLogsDialog");
      },
    },
  },

  methods: {
    setCreatedAtFormat(date) {
      return moment(date).format("MMM D, YYYY h:mm A");
    },
  },
};
</script>
